import React, { useState, useEffect, useRef } from 'react';
import Card from './Card';
import defaultDeck from './data/personality';
import styled from 'styled-components/macro';
import './App.css';
import VisibilityIcon from '@material-ui/icons/Visibility';
import BackIcon from '@material-ui/icons/NavigateBefore';
import NextIcon from '@material-ui/icons/NavigateNext';
import { BottomNavigation, BottomNavigationAction, FormHelperText } from '@material-ui/core';
import TopN from './TopN';
import ArrowKeysReact from 'arrow-keys-react';
import SwipeableViews from 'react-swipeable-views';
import DeckSelectorPage from './DeckSelectorPage';
import FlipCard from './FlipCard/FlipCard';
import Fab from '@material-ui/core/Fab';

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

const history = [];

const Container = styled.div`
background-color:indianred;
position:absolute;
top:0;
right:0;
  height:100vh;
  width:100vw;
`

function App() {

  const appRef = useRef();
  // useEffect(() => appRef.current.focus());

  ArrowKeysReact.config({
    left: handleBack,
    right: handleNext,
    down: () => setShowAnswer(true),
  });

  const [deck, setDeck] = useState(defaultDeck);
  const [selectedDeck, setSelectedDeck] = useState('Words and Expressions');
  const [wordIndex, setWordIndex] = useState(0);
  const [view, setView] = useState(0);
  const [reverse, toggleReverse] = useState(false);
  const [animate, setAnimate] = useState(true);
  const [back, setBack] = useState(false);
  const [showAnswer, setShowAnswer] = useState(false);

  function handleNext() {
    const _deck = [...deck];
    _deck.splice(wordIndex, 1);
    setDeck(_deck);
    const _wordIndex = wordIndex + 1;
    history.push(_wordIndex);
    setBack(false);
    setAnimate(true);
    setShowAnswer(false);
    setWordIndex(_wordIndex);
  }

  function handleBack() {
    history.pop();
    setBack(true);
    setAnimate(true);
    setShowAnswer(false);
    setWordIndex(history[history.length - 1] || 0);
  }

  function handleReverse() {
    setAnimate(false);
    toggleReverse(!reverse);
  }

  function handleViewChange(index, indexLatest, meta) {
    setView(index);
  }

  async function loadDeck({ name, fileName }) {
    const module = await import(`./data/${fileName}`);
    setSelectedDeck(name);
    setDeck(module.default);
    setWordIndex(0);
    // setView(0);
  }

  let question, answer;
  if (!reverse)
    [question, answer] = deck[wordIndex];
  else
    [answer, question] = deck[wordIndex];
  console.log(deck.length);
  return (
    <>
      <Container>
        {
          [...deck].map(([question, answer]) =>
            <FlipCard key={question} id={question} front={question} back={answer}></FlipCard>
          )}
      </Container>
      <Fab
        style={{ position: 'absolute', bottom: 16, right: 16 }}
        color='primary'
        onClick={handleNext}
      >
        <NextIcon />
      </Fab>
    </>
  )
}

export default App;
