import React, { useState } from 'react';
import './FlipCard.css';
import styled from 'styled-components/macro';

export default function FlipCard({ front, back }) {
    const [flipped, setFlipped] = useState(false);
    return (
        <Card flipped={flipped} messiness={5} onClick={() => setFlipped(!flipped)}>
            <Front>
                {front || <h1>Front</h1>}
            </Front>
            <Back>
                {back || <h1>Back</h1>}
            </Back>
        </Card >
    )
}

function calcTransform({ flipped, messiness }) {
    const flip = flipped ? 'rotateY(180deg)' : '';
    const randomRotationZ = messiness ? `rotateZ(${(Math.random() - 0.5) * messiness}deg)` : '';
    const randomTranslateX = messiness ? `translateX(${(Math.random() - 0.5) * messiness}px)` : '';
    const randomTranslateY = messiness ? `translateY(${(Math.random() - 0.5) * messiness}px)` : '';
    return `${flip} ${randomRotationZ} ${randomTranslateX} ${randomTranslateY}`;
}

const Card = styled.div`
    will-change:transform;
    position: fixed;
    width: 90vw;
    height: 90vh;
    margin: 5vh 5vw;
    background-color: transparent;
    perspective: 1000px;
    text-align: center;
    transition: transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transform-style: preserve-3d;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    transform: ${calcTransform};
    font-size: 2.5em;
    font-family: Rubik;
    border-radius: 12px;
    border: 1px solid #3336
`

const Front = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #FFF;
    color: #000A;
    backface-visibility: hidden;
    z-index: 2;
    display:flex;
    justify-content:center;
    align-items:center;
    border-radius: 10px;
`

const Back = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    /* background-color: #2980b9; */
    background-color: #DDD;
    color: #000A;
    transform: rotateY(180deg);
    backface-visibility: hidden;
    z-index: 1;
    display:flex;
    justify-content:center;
    align-items:center;
    border-radius: 10px;
`
