import React, { useRef } from 'react'
import styled from 'styled-components/macro';
import Flip from 'react-reveal/Flip';
import Fade from 'react-reveal/Fade';
import IconButton from '@material-ui/core/IconButton';
import SwapVertIcon from '@material-ui/icons/SwapVert';

const Container = styled.div`
    margin-top:10%;
    margin-bottom: 10%;
    height:80%;
    display: grid;
    display:-ms-grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    -ms-grid-columns: 1fr;
    -ms-grid-rows: 1fr 1fr 1fr;
`;

const Top = styled.div`
    height:100%;
    grid-row: 1;
    grid-column: 1;
    -ms-grid-row: 1;
    -ms-grid-column: 1;
    display: flex;
    align-items:center;
    justify-content: center;
    text-align:center;
    font-size: 2.5em;
    font-family: Rubik;
    color: white;
`;

const Middle = styled.section`
    height:100%;
    grid-row: 2;
    grid-column: 1;
    -ms-grid-row: 2;
    -ms-grid-column: 1;
    display: flex;
    align-items:center;
    justify-content: center;
`;

const Bottom = styled.div`
    height:100%;
    grid-row: 3;
    grid-column: 1;
    -ms-grid-row: 3;
    -ms-grid-column: 1;
    display: flex;
    align-items:center;
    justify-content: center;
    text-align:center;
    font-size: 2.5em;
    font-family: Rubik;
    color: white;
`;


function Button(props) {
    const children = React.cloneElement(props.children, { fontSize: 'large' });
    return <IconButton style={{ color: 'white', opacity: props.disabled ? 0.2 : 0.9, transition: 'opacity 0.3s linear' }} {...props} > {children}</IconButton >
}


export default ({ question, answer, showAnswer, animate, ...props }) => {

    const fadeDirection = props.history ? { left: true } : { right: true };

    const topRef = useRef();
    const botRef = useRef();

    const onReverse = e => {
        const { offsetTop: topY, style: topStyle } = topRef.current;
        const { offsetTop: botY, style: botStyle } = botRef.current;
        // const transition = 'transform 0.2s ease-in-out';
        const transition = 'transform 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55)';
        // -webkit-transition: all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
        topStyle.transition = transition;
        botStyle.transition = transition;
        topStyle.transform = `translateY(${botY - topY}px)`;
        botStyle.transform = `translateY(${topY - botY}px)`;

        const onTransitionEnd = () => {
            window.removeEventListener('transitionend', onTransitionEnd);
            props.onReverse();
        }
        window.addEventListener('transitionend', onTransitionEnd);
        // props.onReverse();
    }

    return (
        <Container>
            <Top ref={topRef}>
                <Fade {...fadeDirection} duration={700} when={true} appear={animate}>
                    {question}
                </Fade>
            </Top>
            <Middle>
                <Button onClick={onReverse} disabled={!showAnswer} ><SwapVertIcon fontSize='large' /></Button>
            </Middle>
            <Bottom ref={botRef} style={{ visibility: showAnswer ? 'visible' : 'hidden' }}>
                <Flip top slide duration={700} when={showAnswer}>
                    {answer}
                </Flip>
            </Bottom>
        </Container >
    )
}
